import React, {useState, useRef} from 'react'
import "../../Style/AddEventBlock.scss"
import AddEventDetailInfo from './AddEventDetailInfo.js';
import Warning from '../../UtilComponent/Warning.js';
import AddImgBlock from './AddImgBlock.js';

import { createCurrentDate, postFetch, getTourApi } from "../../CommonFunction/commonFunction.js" 
import he from 'he';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPlus, faImage, faXmark, faSpinner } from '@fortawesome/free-solid-svg-icons'

import {  useDispatch, useSelector } from 'react-redux';
import {  showAddEventBlock  } from '../../features/handleBoolean/toggleBlock.js';
import {  fetchMainPage  } from '../../features/handleBoolean/toggleFetchData.js';
import {  updateToDefault  } from '../../features/handleInput/inputObj.js';
import { updateWarnText, showWarnBlock, setWarnConfirmFunc, hideScroll } from '../../features/handleBoolean/toggleWarnBlock.js';
import { addNewImgToList, removeAllImgFromList } from '../../features/handleAddImgs/addImgList.js';



export default function AddEventBlock() {
    const [imageSizeList, setImageSizeList] = useState([]);
    const [saving, setSaving] = useState(false);
    const [ifImgLoading, setIfImgLoading] = useState(false);
    const [formData, setFormData] = useState({name: '',tags: '',img: '',event:[]});
    const [steps, setSteps] = useState(1);

    const timeDetail = useSelector((state) => state.inputObj.timeDetail);
    const ifShowWarnBlock = useSelector((state) => state.toggleWarnBlock.ifShowWarnBlock);
    const screenWidth = useSelector((state) => state.screenSize.screenWidth);
    const imgList = useSelector((state) => state.addImgList.imgList);
    const selectedMainImgIndex = useSelector((state) => state.addImgList.selectedMainImgIndex);
    const smallScreen900 = screenWidth <= 900;

    const dispatch = useDispatch();
    const removeAddEvent = () =>{
        if(saving) return;
        dispatch(removeAllImgFromList());
        dispatch(updateToDefault());
        dispatch(showAddEventBlock(false));
    } 

    const uploadNewImg = (e)=>{
        if(steps !== 2) setSteps(2);
        if(ifImgLoading) return;
       setIfImgLoading(true);
       setTimeout(() => {
        let file = e.target.files[0];
        var reader = new FileReader();
        reader.onload = function(e) {
            setIfImgLoading(false);
            let img = new Image();
            img.onload = function() {
                dispatch(addNewImgToList(e.target.result))

                setImageSizeList(prevList => [...prevList, img.width / img.height]);
            };
            img.src = e.target.result;
       };
       reader.readAsDataURL(file);
       }, 500);
    }

  const saveAddEvent = ()=>{
    if(saving) return;
    //empty check
    if(!formData["name"]){
        dispatch(updateWarnText("請輸入行程名稱"));
        dispatch(showWarnBlock(true));
        dispatch(hideScroll(true));
        setWarnConfirmFunc(null);
        return;
    }
    let emptySubName = timeDetail.find(item=>item["name"].length === 0);
    if(emptySubName){
        dispatch(updateWarnText("請輸入地點"));
        dispatch(showWarnBlock(true));
        dispatch(hideScroll(true));
        setWarnConfirmFunc(null);
        return;
    }
    setSaving(true);


    setTimeout(() => {
        let imgList = document.querySelectorAll(".inseryImgItem");
        async function convertImagesToBlob(imgList){
            const promises = [];
            for(let i = 0; i < imgList.length; i++){
                let imgItem = imgList[i];
                console.log(imgItem.width);
                console.log(imgItem.height);
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');
                canvas.width = 900;
                canvas.height = 900 / imageSizeList[i];
                ctx.drawImage(imgItem, 0, 0, canvas.width, canvas.height);

                const promise = new Promise(resolve => {
                    canvas.toBlob(blob => {
                      resolve(new File([blob], "testing.webp", { type: "image/webp" }));
                    },'image/webp', 0.7);
                  });
              
                promises.push(promise);
            }
            const blobs = await Promise.all(promises);
            return blobs;
        }

        convertImagesToBlob(imgList).then(res=>{
            //tags organize
            let tagsStr = formData["tags"];
            let isTagStr = false;
            let finalTagStr = "";
            for(let i = 0; i < tagsStr.length; i++){
            let str = tagsStr[i];
            if(str === "#"){
                isTagStr = true;
            }else if(str === " "){
                isTagStr = false;
            }
            if(isTagStr) finalTagStr += str;
            }
            const submitFormData = new FormData();

            submitFormData.append("1000130", he.escape(formData["name"]));
            submitFormData.append("1000131", createCurrentDate());
            submitFormData.append("1000132", he.escape(finalTagStr));
            res.forEach(item=>{
                submitFormData.append("1000148", item);   
            })
            submitFormData.append("1000140", 0);
            submitFormData.append("1000141", 0);

            for(let i = 1; i <= timeDetail.length; i++){
              const item = timeDetail[i-1];
              submitFormData.append("1000134_"+-i,he.escape(item["time"]));
              submitFormData.append("1000135_"+-i,he.escape(item["type"]));
              submitFormData.append("1000136_"+-i,he.escape(item["name"]));
              submitFormData.append("1000137_"+-i,he.escape(item["content"]));
            }

            postFetch(getTourApi(),submitFormData).then(result=>{
                if(result["status"] === "SUCCESS") {
                    localStorage.setItem(result["ragicId"]+"_owner", 1);
                    setSaving(false);
                    removeAddEvent();
                    dispatch(fetchMainPage(true));
                    dispatch(updateToDefault());
                }
            })
        });
   }, 500);
  }
  
  return (  
    <>
        <div className='addEventBlock'  style={steps === 2 && !smallScreen900? {width:"800px"}:{}} >
            <div className='addEventTitleContainer flexCenter'>
                <div className='cancelAddNewEvent' onClick={removeAddEvent} style={saving?{cursor:"not-allowed",pointerEvents:"none"}:{cursor:"pointer", pointerEvents:"auto"}}>取消</div>
                <div >新增行程</div>
                {steps === 2? <div className='saveAddNewEvent' onClick={saveAddEvent}>
                               {saving? <div className='saveLoading'></div>:""}
                                儲存
                              </div>:null
                } 
            </div>
            {steps === 1? 
            <div className='addEventImgContainer'>
                    <div className='imgUploadContainer flexCenter'>
                        <FontAwesomeIcon icon={faImage} className="navIcons" />
                        <h3>來上傳一張行程照片吧!!</h3>
                        <label htmlFor="fileInput">從電腦選擇</label>
                        <input accept='.jpg,.png,.webp' onChange={uploadNewImg} id="fileInput" type="file" style={{display: "none"}}></input>
                    </div>
            </div>:null}
            {steps === 2? 
               <div className='addEventDetailContainer'>
                    <div className='savingDarkBackground' style={saving?{display:"block"}:{display:"none"}}></div>
                        <div className='imgUploadContainer'>
                            <div className="spinner"></div>
                            {imgList[selectedMainImgIndex]?   
                             <>
                                 <img id='selectedMainAddImage' src={imgList[selectedMainImgIndex]} alt='Tour Photo'></img>
                                 <div className='addMoreImgsContainer'>
                                        {imgList.map((item, index)=>{
                                            return  <AddImgBlock index={index} item={item}/>
                                        })}
                                        {ifImgLoading?  
                                        <div className='addMoreImgsTempItem flexCenter'>                            
                                            <FontAwesomeIcon style={{fontSize:"14px"}} icon={faSpinner} className="navIcons" spin />
                                        </div>:null}
                                        <div className='addMoreImgsItemIcon'>
                                            <label htmlFor="fileInputForMoreImgs">
                                                    <input accept='.jpg,.png,.webp' onChange={uploadNewImg} id="fileInputForMoreImgs" type="file" style={{display: "none"}}></input>
                                                    <FontAwesomeIcon  icon={faPlus} className="navIcons" />
                                            </label>
                                        </div>
                                 </div>
                             </>:null}
                        </div>
                        <AddEventDetailInfo formData={formData} setFormData={setFormData}/>
                </div>:null
            }
        </div>
        <div onClick={removeAddEvent} className='background flexCenter' >
              <div onClick={removeAddEvent} className='xmarkContainer'>
                  <FontAwesomeIcon icon={faXmark} className="navIcons" />
              </div>
        </div>
        {ifShowWarnBlock? <Warning  />:null}
    </>
  )
}
