import "../../Style/AddTimeDetail.scss"
import { updateTimeDetail } from '../../features/handleInput/inputObj.js';
import {  useDispatch } from 'react-redux';

export default function AddTimeDetail({data, index}) {
    const dispatch = useDispatch();

    const timeOptions = [];
    for(let hour = 0; hour < 24; hour++){
        const formattedHour = hour.toString().padStart(2, '0');
        timeOptions.push(formattedHour+":00");
    }
    const handleInputChange = (e)=>{
        const { name, value } = e.target;
        dispatch(updateTimeDetail({name, value, index}));
    }
  return (
    <div  className='addTimeDetailContainer'>
        <div className='timeDetailBasicInfo'>
            <select name='time' onChange={handleInputChange}>
                {timeOptions.map(item=>{
                    return <option>{item}</option>
                })}
            </select>
            <select  name='type' onChange={handleInputChange}>
                <option value="faUtensils">餐廳</option>
                <option value="faLandmark">景點</option>
                <option value="faBagShopping">購物</option>
                <option value="faHouse">住宿</option>
                <option value="faMusic">音樂</option>
                <option value="faCar">交通</option>
                <option value="faMugSaucer">咖啡</option>
                <option value="faBicycle">單車</option>
                <option value="faMountainSun">登山</option>
                <option value="faCableCar">纜車</option>
                <option value="faSchool">教育</option>
                <option value="faShip">搭船</option>
                <option value="faTrain">火車</option>
            </select>
            <input type='text' placeholder='地點' name='name' onChange={handleInputChange} value={data["name"]}></input>
        </div>
        <div className='insertTourContentContainer'>
            <textarea className='insertTourContent' rows={3} name='content' onChange={handleInputChange} value={data["content"]}></textarea>
        </div>
    </div>
  )
}
