import {useState, useRef} from "react";
import AddTimeDetail from "./AddTimeDetail";
import "../../Style/AddEventDetailInfo.scss"

import { getFetch, getArrBySeq, getTagApi } from "../../CommonFunction/commonFunction.js" 

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPlus} from '@fortawesome/free-solid-svg-icons'
import { faTrashCan } from '@fortawesome/free-regular-svg-icons'

import {  useDispatch, useSelector } from 'react-redux';
import {  addNewTimeDetail , removeTimeDetail  } from '../../features/handleInput/inputObj.js';
import {  updateTagHintArr  } from '../../features/handleInput/tagHint.js';



export default function AddEventDetailInfo({formData, setFormData}) {
    const debounceTimer = useRef(null);
    const dispatch = useDispatch();
    const timeDetail = useSelector((state) => state.inputObj.timeDetail);
    const tagHintArr = useSelector((state) => state.tagHint.tagHintArr);


    const handleInputChange = (e)=>{
        const { name, value } = e.target;
        setFormData({...formData, [name]: value});    

        if(name === "tags"){
            let tagHintText = "";
            let ifFetchTagHint = false;
            for(let i = value.length-1; i >= 0; i--){
                let char = value[i];
                if(char !== "#" && char !== " "){
                    tagHintText = char + tagHintText;
                }
                else if(char === " "){
                    ifFetchTagHint = false;
                    break;
                }else if(char === "#"){
                    ifFetchTagHint = true;
                    break;
                }
            }
            if(tagHintText && ifFetchTagHint){
                let ifFullChar = true;
                if (/[\u4E00-\u9FA5]/.test(tagHintText[tagHintText.length-1])) {
                    ifFullChar = true;
                } else if (/[\u3100-\u312F\u31A0-\u31BF\u4E00-\u9FA5]/.test(tagHintText[tagHintText.length-1])) {
                    // 注音
                    ifFullChar = false;
                }
                if(ifFullChar){
                    clearTimeout(debounceTimer.current);
                    debounceTimer.current = setTimeout(() => {
                        getFetch( getTagApi()+"&subtables=0&order=1000144,DESC&limit=50&offset=0&where=1000143,regex,^"+tagHintText).then(result=>{
                            dispatch(updateTagHintArr(getArrBySeq(result)));
                        })
                    }, 300); 
                }
            }else{
                clearTimeout(debounceTimer.current);
                dispatch(updateTagHintArr([]));
            }
        }
    }
    const removeNewTimeDetail = ()=>{
        if(timeDetail.length === 1) return;``
        dispatch(removeTimeDetail());
    }
    const handleSelectTagHint = (e, item)=>{
        let tagHintName = "#" + item["tag"];
        let currentTypeTag = formData["tags"];
        let newTagName = "";
        for(let i = currentTypeTag.length -1; i >= 0; i--){
            let char = currentTypeTag[i];
            newTagName = char + newTagName;
            if(char === "#") break;
        }
        let organizeTag = currentTypeTag.substring(0, currentTypeTag.length - newTagName.length);
        setFormData({...formData, ["tags"]: organizeTag + tagHintName + " "});    
    }
  return (
    <div className='addEventDetail'>
        <div className='insertTitleContainer'>
            <input className='addEventDetailInput' name='name' onChange={handleInputChange} value={formData.name} type='text' placeholder='行程名稱'></input>
        </div>
        <div className='insertTagContainer'>
            <input onClick={(e)=>e.stopPropagation()} className='addEventDetailInput'  type='text' placeholder='新增標籤 : #台北 #餐廳' name='tags' onChange={handleInputChange} value={formData.tags}></input>
            <div className="autocompleteList" style={tagHintArr.length === 0? {display:"none"}:{display:"block"}}>
                <div className='autocompleteListContainer'>
                    {tagHintArr.map(item=>{
                        if(item){
                            return (
                            <div onClick={(e)=>handleSelectTagHint(e, item)} className='flexBetween'>
                                <span>{"#"+item["tag"]}</span>
                                <span>{item["count"]+"則"}</span>
                            </div>
                            )
                        }
                    })}
                </div>
            </div>
        </div>
        {timeDetail.map((item,index)=>{
            return <AddTimeDetail data={item} index={index}/>
        })}
        <div className='addNewTourIconContainer flexCenter'>
            <div className="tourDetailButton flexCenter" onClick={()=>dispatch(addNewTimeDetail())}>
                <FontAwesomeIcon icon={faPlus} className="navIcons" />
            </div>
            <div className="tourDetailButton flexCenter" onClick={ removeNewTimeDetail} style={timeDetail.length === 1? {cursor:"not-allowed"}:{}}>
                <FontAwesomeIcon icon={faTrashCan} className="navIcons" />
            </div>
        </div>
    </div>
  )
}
